  font-size: 0.7em;
  font-weight: 600;
  letter-spacing: normal;
  line-height: 1.476;
  margin-top: 0.15em;
}


/* Block: Table ------------------------------ */

.editor-styles-wrapper .wp-block-table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  font-size: 18px;
  margin-bottom: 1.1em;
  width: 100%;
}

.editor-styles-wrapper .wp-block-table,
.editor-styles-wrapper .wp-block-table * {
  border-color: #dcd7ca;
}

.editor-styles-wrapper .wp-block-table tr {
  border: none;
}

.editor-styles-wrapper .wp-block-table caption {
  background: #dcd7ca;
  text-align: center;
}

.editor-styles-wrapper .wp-block-table th,
.editor-styles-wrapper .wp-block-table td {
  line-height: 1.4;
  margin: 0;
  overflow: visible;
  padding: 0;
}

.editor-styles-wrapper .wp-block-table .wp-block-table__cell-content {
  padding: 0.5em;
}

.editor-styles-wrapper .wp-block-table thead {
  vertical-align: bottom;
  white-space: nowrap;
  text-align: inherit;
}

.editor-styles-wrapper .wp-block-table th {
  font-weight: 700;
  text-align: inherit; /* Prevents the header from being centered by default*/
}

.editor-styles-wrapper .wp-block-table th.has-text-align-center {
  text-align: center;
}

.editor-styles-wrapper .wp-block-table th.has-text-align-right {
  text-align: right;
}

.editor-styles-wrapper .wp-block-table th.has-text-align-left {
  text-align: left;
}

/* STYLE: STRIPES */

.editor-styles-wrapper .wp-block-table.is-style-stripes {
  border: 1px solid #dcd7ca;
}

.editor-styles-wrapper .wp-block-table.is-style-stripes tbody tr:nth-child(odd) {
  background: #dcd7ca;
}


/* Block: Separator -------------------------- */

hr.wp-block-separator {
  border-top: 1px solid #6d6d6d;
  color: #6d6d6d;
  margin: 30px 0;
}

hr.wp-block-separator:not(.is-style-wide):not(.is-style-dots) {
  max-width: 100%;
}

hr.wp-block-separator:not(.is-style-dots) {
  background: linear-gradient(to left, currentColor calc(50% - 16px), transparent calc(50% - 16px), transparent calc(50% + 16px), currentColor calc(50% + 16px));
  background-color: transparent !important;
  border: none;
  height: 1px;
  overflow: visible;
  position: relative;
}

.wp-block-separator.has-background:not(.is-style-wide):not(.is-style-dots) {
  height: 1px;
}

hr.wp-block-separator:not(.is-style-dots)::before,
hr.wp-block-separator:not(.is-style-dots)::after {
  background: currentColor;
  content: "";
  display: block;
  height: 16px;
  position: absolute;
  top: calc(50% - 8px);
  transform: rotate(22.5deg);
  width: 1px;
}

hr.wp-block-separator::before {
  left: calc(50% - 5px);
}

hr.wp-block-separator::after {
  right: calc(50% - 5px);
}

/* STYLE: DOTS */

hr.wp-block-separator.is-style-dots::before {
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 1em;
  padding-left: 1em;
}


/* Block: Quote ------------------------------ */

.editor-styles-wrapper blockquote {
  margin: 0;
}

.editor-styles-wrapper .wp-block-quote {
  border-color: #cd2653;
  border-style: solid;
  border-width: 0 0 0 2px;
  margin: 20px 0;
  padding: 5px 0 5px 20px;
}

.editor-styles-wrapper .wp-block-quote.has-text-align-center,
.editor-styles-wrapper .wp-block-quote[style*="text-align:center"],
.editor-styles-wrapper .wp-block-quote[style*="text-align: center"] {
  border-width: 0;
  padding: 5px 0;
}

.editor-styles-wrapper .wp-block-quote.has-text-align-right,
.editor-styles-wrapper .wp-block-quote[style*="text-align:right"],
.editor-styles-wrapper .wp-block-quote[style*="text-align: right"] {
  border-width: 0 2px 0 0;
  padding: 5px 20px 5px 0;
}

.editor-styles-wrapper cite,
.editor-styles-wrapper .wp-block-quote__citation,
.editor-styles-wrapper .wp-block-quote cite,
.editor-styles-wrapper .wp-block-quote footer {
  color: #6d6d6d;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.25;
}

.editor-styles-wrapper .wp-block-quote p {
  color: inherit;
  font-weight: 400;
  margin: 0 0 20px 0;
}

.editor-styles-wrapper .wp-block-quote.is-style-large {
  border: none;
  padding: 0;
}

.editor-styles-wrapper .wp-block-quote.is-style-large p {
  font-family: inherit;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: -0.035714286em;
  line-height: 1.285714286;
}

.editor-styles-wrapper .wp-block-quote.is-style-large .wp-block-quote__citation,
.editor-styles-wrapper .wp-block-quote.is-style-large cite,
.editor-styles-wrapper .wp-block-quote.is-style-large footer {
  font-size: 16px;
}


/* Block: Code, Verse and Preformatted ------- */

.editor-styles-wrapper .wp-block-code {
  color: inherit;
}

.editor-styles-wrapper .wp-block-code,
.editor-styles-wrapper .wp-block-preformatted pre,
.editor-styles-wrapper .wp-block-verse pre {
  border: 1px solid #dcd7ca;
  border-radius: 0;
